<template>
  <div>
    <div class="attention_item" v-for="(item, index) in result" @touchend="dropDown" :key="index">
      <div>
        <img :src="item.icon" alt="任务ICON">
        <div class="teacher_base_info">
          <a>{{item.detailsValue}}</a><br>
          <a class="home_work">{{new Date(item.inTime).toLocaleString().replace(/\//g,'.').replace(/上午|下午/,'')}}</a>
        </div>
      </div>
      <div class="note" @click="to_flow(item)"><a>+{{item.intergral}}</a>积分</div>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        result: [],
        falgNoData: false,
        page: 1,
        pagination: true,
        showData: false,
      }
    },
    methods: {
      dropDown() {
        if (this.falgNoData) { //数据全部加载完了
          return
        } else if (this.scrollEvent() && this.pagination) { //如果满足触发条件，执行
          this.$Indicator.open({
            text: '加载中...',
            spinnerType: 'fading-circle'
          })
          this.page += 1
          this.axios.post('/api/integralAccess', {
            page: this.page
          }).then(res => {
            if(res.data.result.list && res.data.result.list.length > 0){
              this.result = this.result.concat(res.data.result.list)
              this.$Indicator.close()
            }else{
              this.$Indicator.close()
              this.$Toast({
                message: '没有更多课程了',
                position: 'middle',
                duration: 1000
              })
            }
          }).catch(err => {
            console.log(err)
          })
        }
      },
    },
    mounted() {
      this.$Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
      this.axios.post('/api/integralAccess',{
        page:this.page
      }).then(data => {
        this.result = data.data.result.list
        this.$Indicator.close()
      }).catch(err => {
        console.log(err)
      })
    }
  }
</script>


<style lang="less" scoped>
  /* 关注列表 START */
  .attention_item {
    display: -webkit-flex;
    display: flex;
    background-color: #fff;
  }
  .attention_item>div:nth-child(1) {
    -webkit-box-flex: 7;
    flex: 7;
    padding: .625rem 0 0 .625rem;
  }
  .attention_item>div:nth-child(1)>img {
    width: 1.875rem;
    float: left;
    margin-top: .625rem;
  }
  .attention_item>div:nth-child(1)>div {
    padding: .625rem 0 0 3.75rem;
  }
  .attention_item>div:nth-child(2) {
    -webkit-box-flex: 3;
    flex: 3;
  }
  .attention_item>div:nth-child(2) {
    padding-top: 1.5625rem;
  }
  .attention_item>div:nth-child(2)>a {
    border-radius: .625rem;
    padding: .625rem .9375rem;
  }
  .home_work {
    color: #aaa;
    font-size: .875rem;
    display: inline-block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 12.5rem;
    color: #777;
  }
  .note {
    color: #0dc04a;
    &>a {
      color: #0dc04a;
    }
  } // .notes>a {
  //   background-color: #fff;
  //   color: #777;
  //   border: .0625rem solid #fff;
  // }
  .already_attention {
    background-color: #fff !important;
    color: #777 !important;
    border: .0625rem solid #aaa !important;
    font-size: .75rem;
    padding: .625rem .8125rem !important;
  }
  /* 关注列表 END */
</style>


